import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';
import '../App.css';

class Projects extends Component {
    constructor(props) {
        super(props)
        this.state = {matches: window.matchMedia("(max-width:629px)").matches}
    }

    columnNumber() {
        console.log(this.state.matches)
        if(this.state.matches === true) {
            return 4
        }
        else {
            return 3
        }
    }

    render() {
        return(
            <div className="landing-grid" style={{background: '#5ABFC1'}}>
                <Grid className="project-grid">
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="data:image/png;base64,"
                             alt="django-img"
                             id="django-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>Crypto<br></br>Venture</h3>
                            <p>A web application to predict prices of bitcoin and serve as a discussion forum for cryptocurrency enthusiasts</p>
                            <a href="https://github.com/BhavdeepSinghB/Starburst" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/9/9a/Laravel.svg/1200px-Laravel.svg.png"
                             alt="laravel-img"
                             id="laravel-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>Free Freakin Food</h3>
                            <p>A web application to serve as a discussion board for all offers of free food around the SJSU campus</p>
                            <a href="https://github.com/BhavdeepSinghB/FFF/tree/WorkingBackendBranch" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/c3/Python-logo-notext.svg/1200px-Python-logo-notext.svg.png"
                             alt="python-img"
                             id="python-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>PurgeTwitter</h3>
                            <p>A Twitter application built to wipe out a user from the current user's Twitter profile. Takes care of cleaning your social fingerprints with the click of a button
                            </p>
                            <a href="https://github.com/BhavdeepSinghB/PurgeTwitter" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/c3/Python-logo-notext.svg/1200px-Python-logo-notext.svg.png"
                             alt="python-img"
                             id="python-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>TrainBot</h3>
                            <p>A Python based Discord bot to manage creation and distribution of beta invites for the morningstreams website</p>
                            <a href="https://github.com/BhavdeepSinghB/100DaysOfCode/tree/master/TrainBot" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/c3/Python-logo-notext.svg/1200px-Python-logo-notext.svg.png"
                             alt="python-img"
                             id="python-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>CSUEBot</h3>
                            <p>A twitter bot created in Python to automate Social Media management for student organizations</p>
                            <a href="https://github.com/BhavdeepSinghB/CSUEbot" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/c/c3/Python-logo-notext.svg/1200px-Python-logo-notext.svg.png"
                             alt="python-img"
                             id="python-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>WhoIsOn</h3>
                            <p>A Python based Discord bot created to faciliate grouping up for e-sports teams</p>
                            <a href="https://github.com/BhavdeepSinghB/whoison" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="https://www.pngitem.com/pimgs/m/31-312155_c-programming-language-logo-hd-png-download.png"
                             alt="python-img"
                             id="python-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>The App Sitter</h3>
                            <p>An attempt at a minimal clone of the linux kernel's "init" file. Takes care of shutting down/respawning processes based on time</p>
                            <a href="https://github.com/BhavdeepSinghB/the-app-sitter" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="https://cdn4.iconfinder.com/data/icons/logos-3/600/React.js_logo-512.png"
                             alt="c-img"
                             id="c-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>Online Portfolio</h3>
                            <p>The website you're viewing right now! Created as an attempt to learn ReactJS</p>
                            <a href="https://github.com/BhavdeepSinghB/bhavdeepsinghb.github.io" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                    <Cell col={this.columnNumber()} row={6} className="project-cell">
                        <div className="project-img">
                            <img src="data:image/png;base64,"
                             alt="asm-img"
                             id="asm-img"
                            ></img> 
                        </div>
                        <div className="project-desc">
                            <h3>Checkers</h3>
                            <p>A game of single or multiplayer (2) checkers created in pure MIPS assembly.</p>
                            <a href="https://github.com/BhavdeepSinghB/AssemblyCheckers" target="_blank" rel="noopener noreferrer"><i class="fab fa-github"></i></a>
                        </div>
                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Projects;